
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'App',
components: {
Group,
Radio,
XTextarea,
XButton
},
created: function () {
this.$vux.loading.show({
text: '获取数据中'
})

this.$http.get(`${this.apiHost}/api/v1/dailyStatusLog/today`).then((result) => {
const {
morningStatus = 0,
afternoonStatus = 0,
eveningStatus = 0,
morningStatusText = '',
afternoonStatusText = '',
eveningStatusText = '',
time = ''
} = result.data

this.morningStatus = morningStatus
this.afternoonStatus = afternoonStatus
this.eveningStatus = eveningStatus
this.morningStatusText = morningStatusText
this.afternoonStatusText = afternoonStatusText
this.eveningStatusText = eveningStatusText
this.time = time
}).catch((err) => {
this.$vux.alert.show({
title: '获取初始数据失败',
content: err
})
}).finally(() => {
this.$vux.loading.hide()
})
},
data () {
return {
apiHost: '',
time: '',
statusList: [
{
key: 2,
value: '2 - 极美地'
}, {
key: 1.5,
value: '1.5 - 挺好地+'
}, {
key: 1,
value: '1 - 挺好地'
}, {
key: 0.5,
value: '0.5 - 一般地+'
}, {
key: 0,
value: '0 - 一般地'
}, {
key: -0.5,
value: '-0.5 - 凄美地+'
}, {
key: -1,
value: '-1 - 凄美地'
}
],
textareaPlaceholder: '状态备注',
morningStatus: 0,
afternoonStatus: 0,
eveningStatus: 0,
morningStatusText: '',
afternoonStatusText: '',
eveningStatusText: ''
}
},
methods: {
updateStatus () {
const {
morningStatus,
afternoonStatus,
eveningStatus,
morningStatusText,
afternoonStatusText,
eveningStatusText
} = this

this.$vux.loading.show({
text: '提交中'
})

this.$http.post(`${this.apiHost}/api/v1/dailyStatusLog/update`, {
today: {
morningStatus,
afternoonStatus,
eveningStatus,
morningStatusText,
afternoonStatusText,
eveningStatusText
}
}, {
withCredentials: true
}).then((result) => {
if (result.data.success) {
this.$vux.alert.show({
title: '好了',
content: '更新成功'
})
} else {
this.$vux.alert.show({
title: '坏了',
content: '更新失败'
})
}
}).catch((err) => {
console.log(err)
this.$vux.alert.show({
title: '更新失败',
content: err
})
}).finally(() => {
this.$vux.loading.hide()
})
}
}
}



